'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Switch } from '@/components/ui/switch'
import { Separator } from '@/components/ui/separator'
import { useAppContext } from '@/contexts/AppContext'
import { toast } from 'sonner'
import { BottomNavigation } from '@/components/layout/BottomNavigation'

export default function ConfiguracionPage() {
  const router = useRouter()
  const { state, dispatch } = useAppContext()
  const [isDarkMode, setIsDarkMode] = useState(false)
  const [mounted, setMounted] = useState(false)
  
  // Estados locales para la configuraci√≥n
  const [userSettings, setUserSettings] = useState({
    name: state.user.name,
    email: 'usuario@ejemplo.com',
    phone: '+34 600 000 000',
    address: 'Calle Ejemplo, 123',
    city: 'Madrid',
    postalCode: '28001',
    country: 'Espa√±a'
  })

  const [preferences, setPreferences] = useState({
    emailNotifications: true,
    pushNotifications: true,
    smsNotifications: false,
    marketingEmails: true,
    orderUpdates: true,
    language: 'es'
  })

  // Detectar el tema actual al montar el componente
  useEffect(() => {
    try {
      const savedTheme = localStorage.getItem('theme')
      const systemTheme = window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light'
      const currentTheme = savedTheme || systemTheme
      
      setIsDarkMode(currentTheme === 'dark')
      
      // Aplicar el tema al documento
      if (currentTheme === 'dark') {
        document.documentElement.classList.add('dark')
      } else {
        document.documentElement.classList.remove('dark')
      }
    } catch (error) {
      console.error('Error al cargar tema:', error)
      setIsDarkMode(false)
    }
    setMounted(true)
  }, [])

  const handleSaveProfile = () => {
    // Actualizar el nombre del usuario en el contexto
    dispatch({
      type: 'SET_USER',
      payload: {
        ...state.user,
        name: userSettings.name
      }
    })
    
    toast.success('Perfil actualizado correctamente', {
      duration: 2000
    })
  }

  const handleSavePreferences = () => {
    toast.success('Preferencias guardadas correctamente', {
      duration: 2000
    })
  }

  const handleClearData = () => {
    if (confirm('¬øEst√°s seguro de que quieres limpiar todos los datos? Esta acci√≥n no se puede deshacer.')) {
      // Limpiar carrito, favoritos, etc.
      dispatch({ type: 'CLEAR_CART' })
      dispatch({ type: 'SET_FAVORITES', payload: [] })
      
      toast.success('Datos limpiados correctamente', {
        duration: 2000
      })
    }
  }

  const handleThemeToggle = () => {
    try {
      const newTheme = isDarkMode ? 'light' : 'dark'
      
      // Actualizar el estado local
      setIsDarkMode(!isDarkMode)
      
      // Aplicar el tema al documento
      if (newTheme === 'dark') {
        document.documentElement.classList.add('dark')
        document.documentElement.style.colorScheme = 'dark'
      } else {
        document.documentElement.classList.remove('dark')
        document.documentElement.style.colorScheme = 'light'
      }
      
      // Guardar en localStorage
      localStorage.setItem('theme', newTheme)
      
      toast.success(`Modo ${newTheme === 'dark' ? 'oscuro' : 'claro'} activado`, {
        duration: 2000
      })
    } catch (error) {
      console.error('Error al cambiar tema:', error)
      toast.error('Error al cambiar el tema', {
        duration: 2000
      })
    }
  }

  // Evitar hydration mismatch
  if (!mounted) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900 mx-auto mb-4"></div>
          <p className="text-gray-600">Cargando configuraci√≥n...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 transition-colors">
      {/* Header */}
      <div className="bg-white dark:bg-gray-800 shadow-sm">
        <div className="max-w-7xl mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <Button
              variant="ghost"
              onClick={() => router.back()}
              className="text-gray-600 dark:text-gray-300 hover:text-gray-900 dark:hover:text-white"
            >
              ‚Üê Volver
            </Button>
            <h1 className="text-lg font-semibold text-gray-900 dark:text-white">Configuraci√≥n</h1>
            <div className="w-16"></div>
          </div>
        </div>
      </div>

      <div className="max-w-4xl mx-auto px-4 py-8 space-y-8 pb-24">
        {/* Perfil de Usuario */}
        <Card className="dark:bg-gray-800 dark:border-gray-700">
          <CardHeader>
            <CardTitle className="flex items-center space-x-2 dark:text-white">
              <span>üë§</span>
              <span>Informaci√≥n Personal</span>
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="name" className="dark:text-gray-200">Nombre completo</Label>
                <Input
                  id="name"
                  value={userSettings.name}
                  onChange={(e) => setUserSettings({...userSettings, name: e.target.value})}
                  placeholder="Tu nombre completo"
                  className="dark:bg-gray-700 dark:border-gray-600 dark:text-white dark:placeholder-gray-400"
                />
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="email" className="dark:text-gray-200">Correo electr√≥nico</Label>
                <Input
                  id="email"
                  type="email"
                  value={userSettings.email}
                  onChange={(e) => setUserSettings({...userSettings, email: e.target.value})}
                  placeholder="tu@email.com"
                  className="dark:bg-gray-700 dark:border-gray-600 dark:text-white dark:placeholder-gray-400"
                />
              </div>
            </div>
            
            <Button onClick={handleSaveProfile} className="bg-black hover:bg-gray-800 text-white dark:bg-white dark:text-black dark:hover:bg-gray-200">
              Guardar Perfil
            </Button>
          </CardContent>
        </Card>

        {/* Configuraci√≥n de la Aplicaci√≥n */}
        <Card className="dark:bg-gray-800 dark:border-gray-700">
          <CardHeader>
            <CardTitle className="flex items-center space-x-2 dark:text-white">
              <span>‚öôÔ∏è</span>
              <span>Configuraci√≥n de la Aplicaci√≥n</span>
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="space-y-4">
              <div>
                <Label htmlFor="language" className="dark:text-gray-200">Idioma</Label>
                <select
                  id="language"
                  value={preferences.language}
                  onChange={(e) => setPreferences({...preferences, language: e.target.value})}
                  className="w-full mt-1 p-2 border border-gray-300 dark:border-gray-600 rounded-md focus:ring-2 focus:ring-black dark:focus:ring-white focus:border-transparent dark:bg-gray-700 dark:text-white"
                >
                  <option value="es">Espa√±ol</option>
                  <option value="en">English</option>
                  <option value="fr">Fran√ßais</option>
                </select>
              </div>
              
              <Separator className="dark:bg-gray-600" />
              
              <div className="flex items-center justify-between">
                <div>
                  <Label htmlFor="darkMode" className="dark:text-gray-200">Modo oscuro</Label>
                  <p className="text-sm text-gray-600 dark:text-gray-400">Cambiar a tema oscuro</p>
                </div>
                <Switch
                  id="darkMode"
                  checked={isDarkMode}
                  onCheckedChange={handleThemeToggle}
                />
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Estad√≠sticas de la Cuenta */}
        <Card className="dark:bg-gray-800 dark:border-gray-700">
          <CardHeader>
            <CardTitle className="flex items-center space-x-2 dark:text-white">
              <span>üìä</span>
              <span>Estad√≠sticas de tu Cuenta</span>
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-center">
              <div>
                <p className="text-2xl font-bold text-gray-900 dark:text-white">{state.orders.length}</p>
                <p className="text-sm text-gray-600 dark:text-gray-400">Pedidos Realizados</p>
              </div>
              
              <div>
                <p className="text-2xl font-bold text-red-500">{state.favorites.length}</p>
                <p className="text-sm text-gray-600 dark:text-gray-400">Productos Favoritos</p>
              </div>
              
              <div>
                <p className="text-2xl font-bold text-blue-500">{state.cartItemCount}</p>
                <p className="text-sm text-gray-600 dark:text-gray-400">Items en Carrito</p>
              </div>
              
              <div>
                <p className="text-2xl font-bold text-green-500">
                  ‚Ç¨{state.orders.reduce((sum, order) => sum + order.total, 0).toFixed(2)}
                </p>
                <p className="text-sm text-gray-600 dark:text-gray-400">Total Gastado</p>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Acciones de Cuenta */}
        <Card className="dark:bg-gray-800 dark:border-gray-700">
          <CardHeader>
            <CardTitle className="flex items-center space-x-2 text-red-600 dark:text-red-400">
              <span>‚ö†Ô∏è</span>
              <span>Zona de Peligro</span>
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="p-4 bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg">
              <h3 className="font-medium text-red-900 dark:text-red-200 mb-2">Limpiar todos los datos</h3>
              <p className="text-sm text-red-700 dark:text-red-300 mb-4">
                Esto eliminar√° todos tus favoritos, carrito y datos de la aplicaci√≥n. Esta acci√≥n no se puede deshacer.
              </p>
              <Button
                variant="outline"
                onClick={handleClearData}
                className="text-red-600 dark:text-red-400 border-red-300 dark:border-red-600 hover:bg-red-50 dark:hover:bg-red-900/30"
              >
                Limpiar Datos
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Bottom Navigation */}
      <BottomNavigation />
    </div>
  )
}
