'use client'

// Tipos para el sistema de anal√≠ticas
export interface AnalyticsEvent {
  eventName: string
  userId: string
  productId?: number
  category?: string
  searchQuery?: string
  timestamp: Date
  metadata?: Record<string, any>
}

export interface ProductRecommendation {
  productId: number
  score: number
  reason: string
}

export interface UserBehavior {
  viewedProducts: number[]
  searchQueries: string[]
  favoriteCategories: string[]
  purchaseHistory: number[]
  clickEvents: number
  sessionDuration: number
}

// Clase principal de Analytics
class Analytics {
  private events: AnalyticsEvent[] = []
  private sessionStart: Date = new Date()

  // Funci√≥n para trackear eventos
  async trackEvent(
    eventName: string, 
    userId: string, 
    data?: {
      productId?: number
      category?: string
      searchQuery?: string
      metadata?: Record<string, any>
    }
  ): Promise<void> {
    try {
      const event: AnalyticsEvent = {
        eventName,
        userId,
        productId: data?.productId,
        category: data?.category,
        searchQuery: data?.searchQuery,
        timestamp: new Date(),
        metadata: data?.metadata
      }

      // Guardar evento localmente
      this.events.push(event)
      
      // Enviar al endpoint de API
      const response = await fetch('/api/events', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(event)
      })

      if (!response.ok) {
        throw new Error(`Error al enviar evento: ${response.status}`)
      }

      console.log(`Evento trackeado: ${eventName}`, event)
    } catch (error) {
      console.error('Error al trackear evento:', error)
      // Guardar en localStorage como fallback
      this.saveEventToLocalStorage(eventName, userId, data)
    }
  }

  // Funci√≥n para obtener recomendaciones de productos
  async getProductRecommendations(
    userId: string, 
    currentProductId?: number
  ): Promise<ProductRecommendation[]> {
    try {
      // Obtener comportamiento del usuario
      const userBehavior = await this.getUserBehavior(userId)
      
      // Cargar productos desde el archivo JSON
      const productsResponse = await fetch('/api/products')
      const { products } = await productsResponse.json()

      if (!products || products.length === 0) {
        return []
      }

      // Algoritmo de recomendaci√≥n basado en comportamiento
      const recommendations: ProductRecommendation[] = []

      for (const product of products) {
        if (currentProductId && product.id === currentProductId) {
          continue // No recomendar el producto actual
        }

        let score = 0
        let reason = ''

        // 1. Productos de categor√≠as favoritas (peso: 40%)
        if (userBehavior.favoriteCategories.includes(product.category)) {
          score += 40
          reason = `Te gusta la categor√≠a ${product.category}`
        }

        // 2. Productos similares a los vistos (peso: 30%)
        if (userBehavior.viewedProducts.includes(product.id)) {
          score += 30
          reason = reason ? `${reason} y ya viste este producto` : 'Ya viste este producto'
        }

        // 3. Productos populares (peso: 20%)
        const popularity = await this.getProductPopularity(product.id)
        score += popularity * 20

        // 4. Productos con stock disponible (peso: 10%)
        if (product.stock > 0) {
          score += 10
        } else {
          score -= 20 // Penalizar productos sin stock
        }

        // 5. Bonus por b√∫squedas relacionadas
        for (const query of userBehavior.searchQueries) {
          if (product.name.toLowerCase().includes(query.toLowerCase()) ||
              product.description.toLowerCase().includes(query.toLowerCase())) {
            score += 15
            reason = reason ? `${reason} y coincide con tus b√∫squedas` : 'Coincide con tus b√∫squedas'
          }
        }

        if (score > 0) {
          recommendations.push({
            productId: product.id,
            score,
            reason: reason || 'Recomendado para ti'
          })
        }
      }

      // Ordenar por score y retornar top 6
      return recommendations
        .sort((a, b) => b.score - a.score)
        .slice(0, 6)

    } catch (error) {
      console.error('Error al obtener recomendaciones:', error)
      return []
    }
  }

  // Obtener comportamiento del usuario
  private async getUserBehavior(userId: string): Promise<UserBehavior> {
    try {
      const response = await fetch(`/api/events?userId=${userId}&action=behavior`)
      if (response.ok) {
        return await response.json()
      }
    } catch (error) {
      console.error('Error al obtener comportamiento del usuario:', error)
    }

    // Fallback: obtener datos del localStorage
    return this.getUserBehaviorFromLocalStorage(userId)
  }

  // Obtener popularidad de un producto
  private async getProductPopularity(productId: number): Promise<number> {
    try {
      const response = await fetch(`/api/events?productId=${productId}&action=popularity`)
      if (response.ok) {
        const { popularity } = await response.json()
        return Math.min(popularity / 100, 1) // Normalizar entre 0 y 1
      }
    } catch (error) {
      console.error('Error al obtener popularidad del producto:', error)
    }
    return 0
  }

  // Guardar evento en localStorage como fallback
  private saveEventToLocalStorage(
    eventName: string, 
    userId: string, 
    data?: any
  ): void {
    try {
      const storageKey = `analytics_${userId}`
      const existingEvents = JSON.parse(localStorage.getItem(storageKey) || '[]')
      
      existingEvents.push({
        eventName,
        userId,
        ...data,
        timestamp: new Date().toISOString()
      })

      // Mantener solo los √∫ltimos 100 eventos
      if (existingEvents.length > 100) {
        existingEvents.splice(0, existingEvents.length - 100)
      }

      localStorage.setItem(storageKey, JSON.stringify(existingEvents))
    } catch (error) {
      console.error('Error al guardar en localStorage:', error)
    }
  }

  // Obtener comportamiento del usuario desde localStorage
  private getUserBehaviorFromLocalStorage(userId: string): UserBehavior {
    try {
      const storageKey = `analytics_${userId}`
      const events = JSON.parse(localStorage.getItem(storageKey) || '[]')
      
      const behavior: UserBehavior = {
        viewedProducts: [],
        searchQueries: [],
        favoriteCategories: [],
        purchaseHistory: [],
        clickEvents: 0,
        sessionDuration: Date.now() - this.sessionStart.getTime()
      }

      // Analizar eventos para extraer comportamiento
      for (const event of events) {
        switch (event.eventName) {
          case 'product_view':
            if (event.productId && !behavior.viewedProducts.includes(event.productId)) {
              behavior.viewedProducts.push(event.productId)
            }
            break
          case 'search':
            if (event.searchQuery && !behavior.searchQueries.includes(event.searchQuery)) {
              behavior.searchQueries.push(event.searchQuery)
            }
            break
          case 'category_filter':
            if (event.category && !behavior.favoriteCategories.includes(event.category)) {
              behavior.favoriteCategories.push(event.category)
            }
            break
          case 'purchase':
            if (event.productId && !behavior.purchaseHistory.includes(event.productId)) {
              behavior.purchaseHistory.push(event.productId)
            }
            break
          case 'click':
            behavior.clickEvents++
            break
        }
      }

      return behavior
    } catch (error) {
      console.error('Error al obtener comportamiento desde localStorage:', error)
      return {
        viewedProducts: [],
        searchQueries: [],
        favoriteCategories: [],
        purchaseHistory: [],
        clickEvents: 0,
        sessionDuration: 0
      }
    }
  }

  // Obtener estad√≠sticas de la sesi√≥n
  getSessionStats(): { duration: number, eventsCount: number } {
    return {
      duration: Date.now() - this.sessionStart.getTime(),
      eventsCount: this.events.length
    }
  }

  // Limpiar eventos antiguos
  clearOldEvents(daysOld: number = 30): void {
    const cutoffDate = new Date()
    cutoffDate.setDate(cutoffDate.getDate() - daysOld)
    
    this.events = this.events.filter(event => event.timestamp > cutoffDate)
  }
}

// Instancia singleton
const analytics = new Analytics()

// Funciones de conveniencia para exportar
export const trackEvent = analytics.trackEvent.bind(analytics)
export const getProductRecommendations = analytics.getProductRecommendations.bind(analytics)
export const getSessionStats = analytics.getSessionStats.bind(analytics)
export const clearOldEvents = analytics.clearOldEvents.bind(analytics)

export default analytics
