'use client'

import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { useAppContext } from '@/contexts/AppContext'
import { useRouter, usePathname } from 'next/navigation'
import { toast } from 'sonner'
import { trackEvent } from '@/lib/analytics'

export function BottomNavigation() {
  const { state, dispatch } = useAppContext()
  const router = useRouter()
  const pathname = usePathname()
  
  const cartItemsCount = state.cart.reduce((total, item) => total + item.quantity, 0)
  const favoritesCount = state.favorites.length

  const handleNavigation = async (path: string, label: string) => {
    await trackEvent('navigation', state.user.id, {
      metadata: { path, label, from: pathname }
    })

    if (pathname === path || (path === '/' && pathname === '/')) {
      toast.success(`Est√°s en ${label}`, {
        duration: 2000,
        style: {
          background: '#84cc16',
          color: 'black',
          border: 'none'
        }
      })
      return
    }
    
    router.push(path)
  }

  const handleFavorites = async () => {
    await trackEvent('navigation', state.user.id, {
      metadata: { path: '/favoritos', label: 'Favoritos', from: pathname }
    })

    if (pathname === '/favoritos') {
      toast.success('Est√°s en Favoritos', {
        duration: 2000,
        style: {
          background: '#84cc16',
          color: 'black',
          border: 'none'
        }
      })
      return
    }
    
    router.push('/favoritos')
  }

  const handleCart = async () => {
    await trackEvent('cart_open', state.user.id, {
      metadata: { itemsCount: cartItemsCount }
    })

    if (cartItemsCount > 0) {
      dispatch({ type: 'TOGGLE_CART_MODAL' })
    } else {
      toast.info('Tu carrito est√° vac√≠o', {
        duration: 2000
      })
    }
  }

  const handleProfile = async () => {
    await trackEvent('profile_open', state.user.id, {
      metadata: { from: pathname }
    })
    dispatch({ type: 'TOGGLE_PROFILE_MODAL' })
  }

  const isActive = (path: string) => {
    if (path === '/') {
      return pathname === '/' || pathname === '/shop'
    }
    return pathname === path
  }

  return (
    <nav className="fixed bottom-0 left-0 right-0 bg-black dark:bg-gray-900 rounded-t-3xl px-6 py-4 z-50 border-t border-gray-800">
      <div className="flex items-center justify-around max-w-sm mx-auto">
        {/* Home - Instagram style house icon */}
        <Button
          variant="ghost"
          size="sm"
          className={`p-3 h-auto rounded-full transition-all duration-200 ${
            isActive('/') 
              ? 'bg-lime-400 text-black hover:bg-lime-500' 
              : 'text-white hover:bg-gray-800'
          }`}
          onClick={() => handleNavigation('/', 'Inicio')}
        >
          <div className="w-6 h-6 relative">
            {/* Casa estilo Instagram */}
            <div className="absolute bottom-1 left-1/2 transform -translate-x-1/2 w-4 h-3 border-2 border-current"></div>
            <div className="absolute bottom-3 left-1/2 transform -translate-x-1/2 w-0 h-0 border-l-3 border-r-3 border-b-2 border-l-transparent border-r-transparent border-b-current"></div>
            <div className="absolute bottom-1 left-1/2 transform -translate-x-1/2 translate-x-0.5 w-1 h-2 bg-current"></div>
          </div>
        </Button>

        {/* Favorites - Instagram style heart */}
        <Button
          variant="ghost"
          size="sm"
          className={`p-3 h-auto rounded-full transition-all duration-200 relative ${
            isActive('/favoritos') 
              ? 'bg-lime-400 text-black hover:bg-lime-500' 
              : 'text-white hover:bg-gray-800'
          }`}
          onClick={handleFavorites}
        >
          <div className="w-6 h-6 relative">
            {/* Coraz√≥n estilo Instagram */}
            <div className="absolute top-1.5 left-1 w-2 h-2 bg-current rounded-full transform -rotate-45"></div>
            <div className="absolute top-1.5 right-1 w-2 h-2 bg-current rounded-full transform rotate-45"></div>
            <div className="absolute top-2.5 left-1/2 w-3 h-3 bg-current transform -translate-x-1/2 rotate-45"></div>
          </div>
          {favoritesCount > 0 && (
            <Badge 
              variant="destructive" 
              className="absolute -top-1 -right-1 h-5 w-5 rounded-full p-0 flex items-center justify-center text-xs bg-red-500 text-white border-2 border-black"
            >
              {favoritesCount}
            </Badge>
          )}
        </Button>

        {/* Cart - Instagram style shopping bag */}
        <Button
          variant="ghost"
          size="sm"
          className="p-3 h-auto rounded-full text-white hover:bg-gray-800 transition-all duration-200 relative"
          onClick={handleCart}
        >
          <div className="w-6 h-6 relative">
            {/* Bolsa de compras estilo Instagram */}
            <div className="absolute bottom-0 left-1 w-4 h-3 border-2 border-current border-t-0 rounded-b"></div>
            <div className="absolute top-1 left-2 w-2 h-2 border-2 border-current border-b-0 rounded-t-full"></div>
          </div>
          {cartItemsCount > 0 && (
            <Badge 
              variant="destructive" 
              className="absolute -top-1 -right-1 h-5 w-5 rounded-full p-0 flex items-center justify-center text-xs bg-red-500 text-white border-2 border-black"
            >
              {cartItemsCount}
            </Badge>
          )}
        </Button>

        {/* Profile - Instagram style profile circle */}
        <Button
          variant="ghost"
          size="sm"
          className="p-3 h-auto rounded-full text-white hover:bg-gray-800 transition-all duration-200"
          onClick={handleProfile}
        >
          <div className="w-6 h-6 relative">
            {/* Perfil estilo Instagram */}
            <div className="absolute inset-0 border-2 border-current rounded-full"></div>
            <div className="absolute top-1.5 left-1/2 w-2 h-2 bg-current rounded-full transform -translate-x-1/2"></div>
            <div className="absolute bottom-1 left-1/2 w-3 h-2 border-2 border-current border-t-0 rounded-b-full transform -translate-x-1/2"></div>
          </div>
        </Button>
      </div>
    </nav>
  )
}
